html,
#root {
  background-color: rgba(246, 246, 247, 1);

}

.App {
  font-family: 'Ubuntu', 'BlinkMacSystemFont', 'San Francisco', 'Segoe UI', 'Roboto', 'Helvetica Neue',
    sans-serif;
  margin: 4rem auto 1rem auto;
  max-width: 940px;
  position: relative;
  border-radius: 0.5rem;
}

.page {
  background-color: white;
  border-color: #e4e4e5;
}

p {
  font-size: 1em;
  font-weight: 400;
  margin: 0;
}

.notification {
  z-index: 1;
  cursor: pointer;
  position: absolute;
  top: -4rem;
  left: -4.5rem;
  transition: all 1s;
  transform-origin: 50% 50%;
  width: 10.5rem;
  -webkit-animation: breathing 4s ease-out infinite normal;
  animation: breathing 4s ease-out infinite normal;
  -webkit-font-smoothing: antialiased;
}

@-webkit-keyframes breathing {
  0% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  25% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  75% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
}

@keyframes breathing {
  0% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
  }

  25% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
  }

  75% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
  }
}

/* Header */

.header {
  background: url(https://tiktok.omegatheme.com/static/media/header-icons.474a4ba596b4cf1153ad.svg) no-repeat top 0 left 88px;
  background-color: #001f3e;
  background-position: 0;
  background-size: contain;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding-bottom: 3rem;
  width: 100%;
}

.stack {
  color: white;
}

.liner-border {
  -webkit-text-stroke: 5px transparent;
  background: linear-gradient(15deg, #ff661b, #e9ba1f);
  -webkit-background-clip: text;
  background-size: 200% 200%;
  color: #fff;
  font-size: 25px;
  font-weight: 700;
  line-height: 2.5rem;
  position: relative;
  text-transform: capitalize;
}

.bell-img {
  position: absolute;
  top: 4px;
  height: 35px;
  right: -40px;
}

.bell-img:hover {
  cursor: pointer;
  animation: bellshake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
}

.tooltip-inner {
  background-color: white;
  color: black;
}


@keyframes bellshake {
  0% {
    transform: rotate(0);
  }

  15% {
    transform: rotate(10deg);
  }

  30% {
    transform: rotate(-10deg);
  }

  45% {
    transform: rotate(5deg);
  }

  60% {
    transform: rotate(-5deg);
  }

  75% {
    transform: rotate(3deg);
  }

  85% {
    transform: rotate(-3deg);
  }

  92% {
    transform: rotate(1deg);
  }

  100% {
    transform: rotate(0);
  }
}

.stack-title {
  font-size: 23px;
  font-weight: 700;
}

.stack-sub-title {
  font-size: 15px;
  font-weight: 400;
}

.claim-gift-button {
  background-color: #ffb02f;
  border: none;
  border-radius: 20px;
  margin-top: 2rem;
  height: 2.5rem;
  width: 13rem;
  color: white;
  font-size: 1.25rem;

  box-shadow: var(--p-shadow-button);
  color: var(--p-text);
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  position: relative;
  text-align: center;
  text-decoration: none;

}

.claim-gift-button__img {
  height: 23px;
  margin-bottom: 5px;
  margin-right: 8px;
}

.claim-gift-button__text {
  font-size: 20px;
  font-weight: 400;
}

.claim-gift-button:hover {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
}

@keyframes shake {

  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

.video-thumbnail {
  position: relative;
}

.video-thumbnail__play-button {
  background-image: url(https://i.ytimg.com/vi/pICCkyNaIJU/hqdefault.jpg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding-bottom: 56.25%;
  position: relative;
  width: 100%;
  border-radius: 0.75rem;
}

.video-thumbnail__play-icon {
  height: 3.75rem;
  left: 50%;
  margin-left: -1.875rem;
  margin-top: -1.875rem;
  position: absolute;
  top: 50%;
  width: 3.75rem;
}

.video-thumbnail__timestamp {
  background-color: white;
  border-radius: 0.25rem;
  bottom: 0;
  color: rgb(32, 34, 35);
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  opacity: .8;
  padding: 0 0.25rem;
  position: absolute;
  text-align: center;
}

/* Body */

.body-section__layout {
  padding-bottom: 1rem;
}

.body-title {
  color: #001f3e;
  font-size: 22px;
  font-weight: 700;
  margin: auto;
  text-align: center;
}

.feature-card {
  cursor: pointer;
  width: 100%;
  border: none;
  box-shadow: 1px 5px 20px 0px rgba(223, 223, 223, 0.5);
  border-radius: 0.5rem;
}

@media (min-width: 576px) {
  .feature-card {
    margin: 10px 0;
    width: 91%;
  }
}

.feature-card:hover {
  box-shadow: 1px 5px 20px 0px rgba(223, 223, 223, 0.95);

}


.feature-card__title {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.5rem;
  display: flex;
  padding: 1.25rem 0 0 0;

}

.feature-card__body {
  color: #444;
  /* padding: 0; */
  padding: 0.25rem 1.25rem 1.25rem 0;
}

.body-button {
  border: 2px solid #2a4d8f;
  border-radius: 30px;
  background-color: white;
  color: #2a4d8f;
  padding: 0.45rem 3rem;
}

.body-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
  color: #2a4d8f;

}

/* Review */

.review-section__layout {
  margin: 2rem 0;
}

.review-section__title {
  color: #001f3e;
  font-family: Ubuntu, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  text-align: center;
}

.carouse-card {
  margin-top: 20px;
  margin-right: 1rem;
  padding: 1rem 2rem;
  background: rgba(255, 245, 213, 1);
  border-radius: 10px;
  /* height: 19.5rem; */
  width: 18.5rem;

}

.carouse-card__name {
  position: absolute;
  top: 9px;
  color: rgba(23, 167, 154, 1);
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  font-family: 'Ubuntu';
}

.carouse-card__description {
  display: flex;
  text-align: justify;
  padding-top: 1rem;
  height: 20rem;
}

.carouse-card__date {
  display: flex;
  justify-content: space-between;
  top: 15rem;

}

.carouse-card__date img {
  height: 15px;
}

.review-section__container {
  max-width: 90%;
}

.review-section__carousel div>button[data-direction='left'],
.review-section__carousel div>button[data-direction='right'] {

  background: url(https://cdn.dsmcdn.com/web/production/slick-arrow.svg) 50% no-repeat;
  border: none;
  cursor: pointer;
  height: 80px;
  outline: 0;
  position: relative;

}

.review-section__carousel div>button[data-direction='left'] {
  left: -12px;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.review-section__carousel div>button[data-direction='right'] {
  right: -12px;
}

.review-section__carousel>div:first-child,
.review-section__carousel>div:last-child {
  display: flex;
  align-items: center;
}

.review-section__button-view-more,
.review-section__button-view-more:hover {
  color: #17a79a;
  background: transparent;
  border: 0;
  box-shadow: none;

}

.review-section__button-view-more:hover {
  text-decoration: underline;
}

/* Footer */

.footer-section__layout {
  background-color: black;
  color: white;
}

.footer-title {
  font-size: 17px;
  font-weight: 500;
}

.card-back {
  color: rgb(255, 73, 48);
}

.small-text {
  font-size: 12px;
  font-style: italic;
}

.flip-countdown.theme-light {
  --text-color: #fff !important;
  --text-bottom-color: #fff !important;
  --background-color: #ff4930 !important;
  --background-bottom-color: #ff4930 !important;
}

.footer-left>* {
  margin-bottom: 1rem;
}

.flip-countdown-card:after {
  content: ":";
  height: 30px;
  font-size: 25px;
  font-weight: 700;
  margin: 0 15px 0 10px;
}

.flip-countdown-piece:last-child .flip-countdown-card:after {
  content: '';
}

.flip-countdown .flip-countdown-piece .flip-countdown-card .flip-countdown-card-sec .card__top {
  border-bottom: 0 !important;
}